.weekly-inline {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 0.75rem;
  width: 100%;
}

.weekly-every {
  flex: 0 0 180px;
  max-width: 220px;
  min-width: 140px;
}

.weekly-every-input {
  display: inline-flex;
  align-items: center;
  position: relative;
  width: 4.25rem;
}


.weeks-suffix {
  position: absolute;
  right: 0.4rem;
  font-size: 0.9rem;
  color: var(--secondary-text-color);
  pointer-events: none;
}

.weekday-row {
  display: flex;
  gap: 0.4rem;
  flex-wrap: wrap;
  align-items: center;
  flex: 1 1 260px;
  min-width: 0;
}

.weekday-selection {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
  flex: 1 1 320px;
  min-width: 0;
}

.weekday-row-label {
  font-weight: 600;
}

@media (max-width: 720px) {
  .weekly-inline {
    flex-direction: column;
    gap: 0.5rem;
  }

  .weekly-every {
    flex: 1 1 auto;
    max-width: none;
    min-width: 0;
  }

  .weekday-row {
    width: 100%;
    justify-content: flex-start;
  }
}
